import React, { Component } from 'react';
import RegisterBtn from '../components/RegisterBtn';
import logo from '../logo.png'

export default class MainPage extends Component {
  componentDidMount = () => {
    const {dark} = this.props;
    if (dark) {
      document.body.classList.value = "bg-dark"
    }
  }
  render() {
    const {dark} = this.props;
    if (dark) {
      document.body.classList.value = "bg-dark"
    } else {
      document.body.classList.value = "bg-light"
    }
    return (
      <>
        <div className="bg-half animated slideInDown fast" />
        <div className="wrapper d-flex justify-content-center align-items-center">
          <div className="container">
            <div className="card animated slideInUp fast border-0 shadow">
              <div className={`card-body ${(dark) ? 'bg-dark text-white' : (dark ? 'bg-light text-dark' : '')}`}>
                <div className="w-25 mx-auto">
                  <img src={logo} className="mdo-logo m-auto d-block" alt=""/>
                </div>
                <h4 className="mt-4 text-center">Booking Antrian</h4>
                <hr className="my-2" style={{ maxWidth: '50%' }} />
                <p className="mb-4 mt-1 text-center text-muted">Silakan pilih menu</p>
                <RegisterBtn />
              </div>
            </div>
          </div>
        </div>
      </>
    )
  }
}
